import * as THREE from "three";

// https://codesandbox.io/s/oxvn0wj1xy
// https://stackoverflow.com/questions/45353282/how-to-zoom-a-three-js-scene-with-the-mouse-wheel
class Visualizer extends React.Component {
  componentDidMount() {
    document.addEventListener("mousewheel", onDocumentMouseWheel, false);

    //3D SCENE
    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(70, 800 / 600, 1, 3000);
    camera.position.z = 1000;
    camera.name = "camera";
    scene.add(camera);
    const renderer = new THREE.WebGLRenderer({ alpha: true, antialias: true });
    renderer.setSize(800, 600);

    const material = new THREE.MeshBasicMaterial({
      color: 0xeeeeee
    });

    const radius = 900;
    const gap = radius * 0.66;
    const geometry = new THREE.RingGeometry(gap, radius, 4, 1, 0, Math.PI * 2);
    const mesh = new THREE.Mesh(geometry, material);
    scene.add(mesh);
    this.mesh = mesh;

    this.renderer = renderer;
    this.camera = camera;
    this.scene = scene;
    this.mount.appendChild(this.renderer.domElement);
    this.start();
  }

  componentWillUnmount() {
    this.stop();
    this.mount.removeChild(this.renderer.domElement);
  }

  start = () => {
    if (!this.frameId) {
      this.frameId = requestAnimationFrame(this.animate);
    }
  };

  stop = () => {
    cancelAnimationFrame(this.frameId);
  };

  animate = () => {
    this.mesh.rotation.z -= 0.2 * 0.03 + 0.002;
    this.renderScene();
    this.frameId = window.requestAnimationFrame(this.animate);
  };

  renderScene() {
    this.renderer.render(this.scene, this.camera);
  }

  render() {
    return (
      <div
        // style={{width:'400px', height: '400px', left: '0', top:'0' }}
        ref={mount => (this.mount = mount)}
      />
    );
  }
}

export default Visualizer;
